#teamPage{
    margin: 3em 0;
    position: relative;
    max-width: calc(1200px);
    min-height: calc(500px);
    padding: 0.95rem max(5vw, calc(50vw - 570px));

    label.dropdownMenu {
        position: relative;
        display: inline-block;
        gap: 0.5rem;
        line-height: 2rem;
        background-color:#FFFFFF;
        text-align: center;
        .button{
            cursor: pointer;
            align-items: center;
            width: 5em;
            padding: 0.5em 0.5em;
            background-color: #FFEA79;
            font-size: 16px;
            font-weight: bold;
            border-radius: 0.3em;
        }
        > * {
            cursor: pointer;
        }
        input {
            display: none;
        }
        .mask {
            position: absolute;
            top: 100%;
            left: 0%;
            width: 9em;
            background-color: #FFFFFF;
            overflow: hidden;
            transition: height 0.3s;
            cursor: default;
            .content {
                display: flex;
                flex-direction: column;
                > div {
                    transition: background-color 0.3s;
                    p {
                        text-align: start;
                        padding: 0em 1em;
                        font-size: 15px;
                        margin: 0 auto;
                        width: 9em;
                        color: #949494;
                        cursor: pointer;
                        transition: color 0.3s;
                    }
                }
                > div:has(p:hover) {
                    background-color: #cecece;
                    p {
                        color: #000;
                    }
                }
            }
        }
    }
    label.dropdownMenu:has(input:not(:checked)) {
        .mask {
            height: 0;
            border: none;
        }
    }
    label.dropdownMenu:has(input:checked) {
        .mask {
            height: calc(2rem* var(--length));
            border: 1px solid #474141;
        }
    }

    .selectedWork{
        
        .manual{
            h3{
                margin-top: 0em;
            }
            width: 100%;
            position: relative;
        }
        .auto{
            h3{
                margin-top: 0em;
            }
            width: 100%;
            position: relative;
        }

    }

    .selectedWork[data-show="false"] {
        opacity: 0%;
    }

    .header {
        display: flex;
        padding: 0.1em 0.5em;
        font-size: 20px;
        font-weight: bold;
        color: #474141;

        .nameTitle {
            width: 20%;
        }

        p {
            margin-top: 0;
        }
    }

    .groups {
        .group {
            display: flex;
            background-color: #fff;
            border-radius: 3px;
            border: 1px solid #ccc;
            padding: 0.1em 0.5em;
            margin-bottom: 1em;
            
            .name {
                width: 20%;
            }

            .members {
                display: flex;
                flex-wrap: wrap;
                width: 70%;

                p {
                    margin-right: 10px;
                }
            }
        }
    }
}